import { CurrencyPipe } from '@angular/common';
import { Component, HostListener, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { Ecommerccut } from '@biz-nest/commerce-client';
import {
  AppNameService, CurrencyService, EventbusService, MainBarTitleService,
  scrollToTop,
  TooltipComponent,
  WindowService
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';
import { FrontendLogger } from '@biz-nest/core-client';

@Component({
  selector: 'app-ecommerce-cut-view',
  standalone: true,
  imports: [
    RouterModule,
    CurrencyPipe,
    TooltipComponent,
    AdminPageContentComponent,
    AdminTopBarComponent
  ],
  templateUrl: './ecommerce-cut-view.component.html',
  styleUrl: './ecommerce-cut-view.component.scss'
})
export class EcommercCutListComponent
implements OnInit {
  posting = false;
  cleanup?: () => void;
  ecommerceCut?: Ecommerccut;
  addButtText = 'Add Ecommerce cut';
  addButtIcon = 'add';

  loading = false;

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  currentActivePopupId = '';

  constructor(
    public currencyService: CurrencyService,
    private windowService: WindowService,
    private router: Router,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.getEcomCut();
    this.mainBarTitle.title = 'Ecommerce Cuts';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and track your eCommerce cuts for effective financial oversight.'
    });

    scrollToTop(this.windowService);
  }

  updateEcommerccut(ecommerceCut: Ecommerccut) {
    this.navigateByBus(['/ecommerce-cut/update'], 'updateEcommerccut', ecommerceCut);
  }

  addEcommerccut() {
    this.router.navigate(['/ecommerce-cut/add']);
  }

  async getEcomCut() {
    try {
      this.loading = true;
      const found = await Ecommerccut.getAll();


      FrontendLogger.debug('FOUND FOUND', found);

      if (found) {
        this.loading = false;
      }

      if (found && found.dataArr && found.dataArr[0]) {
        FrontendLogger.debug('FOUND FOUND', found.dataArr[0]);
        this.ecommerceCut = found.dataArr[0];
        this.addButtText = 'Update Ecommerce Cut';
        this.addButtIcon = 'visibility';
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }
}
